// Complaints module for lack of professional ethics
import React from "react";
import { Box } from "@mui/material";
import ComplaintForm from "./ComplaintForm";
import "./complaint.css";
import { TiWarning } from "react-icons/ti";

const Complaint = () => {
  return (
    <div className="complaint">
      <Box sx={{ background: "#5c5959", minHeight: "100vh" }}>
        <h1 className="title">
          Mdulo de Denuncias &nbsp;
          <TiWarning
            style={{
              color: "#f9f900",
              margin: "auto",
              verticalAlign: "middle",
            }}
          />
        </h1>
        <p className="subtitle">
          El ejercicio de la tica profesional conlleva el cumplimiento de la
          vocacin, principios, valores y convicciones mostrado en la coherencia
          de las ideas y acciones de cada sujeto, lo cual va ms all de las
          aptitudes y conocimientos adquiridos a lo largo del desarrollo
          acadmico. La falta de la misma podra generar desconfianza,
          incredibilidad e incluso la prdida de su permanencia. Con la tica
          profesional se procuran, no solo los beneficios individuales, sino
          tambin el bien comn. Incumplir con las reglas de manera deliberada,
          puede resultar en sanciones dentro de la empresa, ser penados por la
          ley o incluso poner en peligro la cdula profesional.
        </p>
        <p className="indication">
          Si has sido victima de abuso o sabes de algn incumplimiento respecto
          a la tica profesional puedes levantar tu denuncia en el siguiente
          formulario y te ayudaremos a redactar el reporte correspondiente:
        </p>
        <fieldset>
          <ComplaintForm />
        </fieldset>
      </Box>
    </div>
  );
};

export default Complaint;
